<template>
  <div class="work-space">
      <ul>
        <li>
           <!-- 등록, 수정 -->
          <div v-if="$route.meta.mode !== 'detail'">
            <input type="text" v-model="resultVO.askTitle" placeholder="질문"/>
            <select v-model="resultVO.type">
              <option value="">선택</option>
              <option value="SelectAnswer">객관식</option>
              <option value="TextAnswer">주관식</option>
            </select>
          </div>
          <!-- 상세 -->
          <div v-else>
            <input type="text" v-model="resultVO.askTitle" placeholder="질문" readonly/>
            <select v-model="resultVO.type" disabled>
              <option value="">선택</option>
              <option value="SelectAnswer">객관식</option>
              <option value="TextAnswer">주관식</option>
            </select>
          </div>
        </li>
        <li>
          <AnswerLoader :vo="resultVO" :typeName="resultVO.type" v-on:setAnswerItem="setAnswerItem"/>
        </li>
      </ul>
  </div>
</template>

<script>
import AnswerLoader from '@/components/survey/management/space/answer/AnswerLoader.vue'
export default {
  name: 'Workspace',
  props: ['vo', 'index', 'isLast'],
  components: {
    AnswerLoader
  },
  data () {
    return {
      resultVO: {
        index: '',
        type: '',
        askTitle: '',
        answerItem: []
      }
    }
  },
  created () {
    // 수정, 상세화면 일때
    if (this.$route.meta.mode !== 'register') {
      this.resultVO = this.vo
      if (this.vo.answerItem.length > 0) this.resultVO.answerItem = JSON.parse(this.vo.answerItem)
    }
    this.resultVO.index = this.$props.index
  },
  updated () {
    this.$emit('childUpdated', this.resultVO)
  },
  methods: {
    // 콤포넌트 변경 함수
    settingChange (typeName) {
      this.typeName = typeName
    },
    // 응답문항 set
    setAnswerItem (answerItems) {
      this.resultVO.answerItem = JSON.stringify(answerItems)
      this.$forceUpdate()
    }
  }
}
</script>

<style scoped>
  div.work-space {
    border: 1px solid;
    width: 40em;
    height: auto;
    margin: 10px 0px 10px 0px;
  }
  div.work-space ul {
    list-style: none;
    padding: 30px;
  }
  div.work-space ul li input {
    width: 32em;
    padding: 10px;
    margin: 0px;
  }
   div.work-space ul li select {
    width: 7.5em;
    padding: 10px;
    vertical-align: middle;
  }
</style>



