<template>
    <component :vo="this.vo" :is="component"></component>
</template>

<script>
export default {
  name: 'AnswerLoader',
  data () {
    return {
      component: null
    }
  },
  props: {
    typeName: {type: String, required: true, default: null},
    vo: {required: true, default: null}
  },
  watch: {
    typeName () {
      this.component = () => this.loadAnswer()
    }
  },
  computed: {
    loadAnswer () { return () => import(`@/components/survey/management/space/answer/${this.typeName}.vue`) }
  },
  created () {
    if (this.$route.meta.mode !== 'register') this.component = () => this.loadAnswer()
  }
}
</script>

<style scoped>
</style>



