<template>
  <div class="tool-container">
      <h1>{{$route.meta.title}}</h1>
      <input type="button" value="저장" @click="save"  v-if="$route.meta.mode !== 'detail'"/>
      <input type="button" value="수정" @click="goModify" v-else/>
      <input type="button" value="뒤로가기" @click="goBack" v-if="$route.meta.mode === 'register'"/>
      <input type="button" value="목록" @click="goList" v-else/>
      <!-- 등록,수정 -->
      <div class="head-div" v-if="$route.meta.mode !== 'detail'">
        <ul>
          <li><input type="text" v-model="srvMstNm" placeholder="설문지명" /></li>
          <li><input type="text" v-model="srvMstDc" placeholder="설문지설명" /></li>
          <li><input type="text" v-model="srvMstTg" placeholder="설문지대상" /></li>
        </ul>
      </div>
      <!-- 상세보기 -->
      <div class="head-div" v-else>
        <ul>
          <li><input type="text" v-model="srvMstNm" placeholder="설문지명" /></li>
          <li><input type="text" v-model="srvMstDc" placeholder="설문지설명" /></li>
          <li><input type="text" v-model="srvMstTg" placeholder="설문지대상" /></li>
        </ul>
      </div>
      <div class="content-div">
        <h3>설문지 영역</h3>
        <div class="render-workspace"  v-for="(item, index) in srvMstItems" :key="index">
          <WorkSpace :vo="item" :index="index" :isLast="false" v-on:childUpdated="childUpdated"/>
        </div>
         <!-- 등록,수정 일때만 -->
        <div v-if="$route.meta.mode !== 'detail'" class="content-div-btns">
          <input type="button" value="추가" @click="pushItem"/>
          <input type="button" value="삭제" @click="removeItem"/>
        </div>
      </div>
  </div>
</template>

<script>
import WorkSpace from '@/components/survey/management/space/WorkSpace.vue'
export default {
  name: 'WorkSpaceContainer',
  data () {
    return {
      url: `/action/management/`,
      param: '',
      srvMstNm: '',
      srvMstDc: '',
      srvMstTg: '',
      srvMstItems: []
    }
  },
  created () {
    this.init()
    if (this.$route.meta.mode !== 'register') this.callAjax(this.url, this.param)
  },
  components: {
    WorkSpace
  },
  methods: {
    init () {
      if (this.$route.meta.mode) this.url += this.$route.meta.mode
      if (this.$route.params.srvMstrId) this.param = { value: this.$route.params.srvMstrId }
    },
    // ajax 공통
    callAjax (url, param) {
      this.axios.post(url, param).then((response) => {
        let data = JSON.parse(response.data)
        this.srvMstNm = data.srvMstNm
        this.srvMstDc = data.srvMstDc
        this.srvMstTg = data.srvMstTg
        this.srvMstItems = JSON.parse(data.srvMstItems)
      })
    },
    // 수정하기
    goModify () {
      this.$router.push({name: 'modify', params: {srvMstrId: this.$route.params.srvMstrId}})
    },
    // 목록
    goList () {
      this.$router.push({name: 'list'})
    },
    // 뒤로가기
    goBack () {
      this.$router.go(-1)
    },
    // 설문지 영역 추가
    pushItem () {
      let obj = {
        index: '',
        type: '',
        askTitle: '',
        answerItem: []
      }
      this.srvMstItems.push(obj)
    },
    // 설문지 영역 삭제
    removeItem () {
      this.srvMstItems.pop()
    },
    // 설문지 등록
    save () {
      if (confirm('입력한 설문지내용을 저장하시겠습니까?')) {
        let url = this.$route.meta.mode === 'register' ? '/action/management/create' : '/action/management/update'
        let param = {
          srvMstNm: this.srvMstNm,
          srvMstDc: this.srvMstDc,
          srvMstTg: this.srvMstTg,
          srvMstItems: this.srvMstItems,
          srvMstrId: this.$route.params.srvMstrId
        }
        this.axios.post(url, {value: param}).then((response) => {
          alert('저장되었습니다.')
          this.$router.go(-1)
        })
      }
    },
    // 설문지영역 자식영역 업데이트 반영
    childUpdated (value) {
      this.srvMstItems.splice(value.index, 1, JSON.stringify(value))
    }
  }
}
</script>

<style scoped>
  .tool-container {
    padding: 10px;
    width: 45em;
    height: 100%;
    margin: auto;
  }
  .tool-container div.head-div {
    margin: 30px 0px 30px 0px;
    width: 40em;
    border: 1px solid;
  }
  .tool-container div.content-div {
    margin: 30px 0px 30px 0px;
    width: 40em;
  }
  .tool-container div ul {
    list-style: none;
    padding: 5px;
  }
  .tool-container div ul li {
    padding: 10px;
  }

  .tool-container div ul li input {
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid;
    padding: 10px;
    width: 30em;
  }
</style>



